.blog-header {
  width: 100%;
  height: 70px;
  background: #fff;
  filter: alpha(Opacity=60);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  box-shadow: 2px 2px 10px #ccc;
  position: fixed;
  z-index: 11; }
  .blog-header .blog-text {
    height: 50px;
    font-size: 30px;
    margin-left: 120px;
    display: flex;
    justify-content: flex-start; }
    .blog-header .blog-text .blog-left {
      height: 50px;
      line-height: 50px; }
    .blog-header .blog-text .blog-other {
      height: 20px;
      font-size: 20px;
      padding-left: 20px;
      margin-top: 18px; }
  .blog-header .blog-tab {
    height: 70px;
    width: 400px;
    margin-right: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px; }
    .blog-header .blog-tab .tab {
      text-align: center;
      width: 100px;
      height: 100%;
      cursor: pointer;
      line-height: 70px;
      position: relative; }
      .blog-header .blog-tab .tab .line {
        width: 100%;
        height: 2px;
        position: absolute;
        bottom: 10px;
        background: gold; }

.author-div {
  text-align: center;
  padding: 1rem; }

.author-div div {
  margin-bottom: 1rem; }

.author-introduction {
  font-size: .8rem;
  color: #999; }

.account {
