.prev {
  display: none; }

.next {
  display: none; }

.auto {
  margin: 20px auto;
  text-align: center;
  font-family: "Times New Roman", Georgia, Serif; }

.wrap-v {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column; }
  .wrap-v .iframer-content {
    width: 800px;
    height: 300px;
    background: #555; }
    .wrap-v .iframer-content iframe {
      width: 100%;
      height: 100%; }
  .wrap-v .container {
