.aspect-ratio {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 75%; }

.aspect-ratio iframe {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0; }

.corner-dom {
  position: fixed;
  bottom: 30px;
  right: 50px;
  z-index: 99999;
  width: 50px;
  height: 50px;
  cursor: pointer; }
  .corner-dom div {
    width: 50px;
    height: 50px;
    border-radius: 10px; }

.corner-line {
  width: 100%;
  height: 5px; }

.ding-zi {
  background: #fff;
  border: 2px solid #ccc;
  border-radius: 5px;
  box-shadow: 1px 1px 5px #ccc;
  padding: 0 5px; }
  .ding-zi div {
